<template>
  <div class="home">
    <div class="content">
      <img src="../assets/icon.svg" class="avatar">
      <div class="description" v-html="width > 650 ? msg : msg1"></div>
      <div class="link-wrapper">
        <a href="https://github.com/KoaApi/KoaApi" class="link">GITHUB</a>
        <a href="https://qm88888888.gitbooks.io/koaapi/content/" class="link">DOCUMENT</a>
        <a href="http://weibo.com/u/6446301952" class="link">WEIBO</a>
        <a href="https://qmweb.cc" class="link">BLOG</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      msg: 'Quickly generate the background service interface.',
      msg1: 'Quickly generate the <br> background service interface.'
    }
  },
  computed: {
    width () {
      return document.body.scrollWidth === 0 ? document.documentElement.scrollWidth : document.body.scrollWidth
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
.home
  position absolute 
  top 0
  bottom 0
  left 0
  right 0
  display flex
  align-items center
  .content
    display flex
    flex-direction column
    justify-content center
    align-items center
    flex 1
    height 30%
    .avatar
      flex 1    
      width 160px
    .description
      flex 1
      margin 30px 0
      text-align center
      font-size 24px
      font-weight bold
      color #6289ad
    .link-wrapper
      flex 1
      display flex
      justify-content center
      .link
        flex 1
        display block
        margin-right 36px
        text-decoration none
        color #A9A9A9
        font-size 14px
        &:last-child
          margin 0
        &:hover
          font-weight bold
</style>



