import Home from '@/components/home'

export const libs = [
  {
    path: '/',
    redirect: '/home',
    meta: {
      title: 'koa-api Document'
    },
    component: Home,
    children: [
      {
        path: 'home',
        name: 'home',
        component: Home
      }
    ]
  }
]

export default [
  ...libs
]


